body {
    background-color: #c6a9e9;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

header {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
}

.App form {
    width: 300px;
    margin: 80px auto;
    text-align: center;
    font-weight: 600;
}

.form-control-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    text-align: justify;
}

.form-control-group label {
    margin-bottom: 5px;
    
}

.field {
    border-radius: 3px;
    border-width: 2px;
    border-color: #57016b;
    padding: 2px;
}

.btn{
    background-color: #bb7dc8;
    border: none;
    border-radius: 5px;
    width: 100px;
    font-size: 15px;
}

.btn:hover {
    transform: translateY(-4px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    background-color: #ad73b9;
}

.btn:active {
    transform: translateY(-2px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.error-message {
    color: rgb(228, 44, 68);
    font-weight: 500;
    text-align: center;
}

.footer {
    display: flex;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 10px;
}
